import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, SerieGER } from '../../../../models/dtos/ger/serieGER';

interface SerieEditViewProps extends EditViewProps<SerieGER> {}

const SerieEditView: FC<SerieEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<SerieGER>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 110;

  return (
    <WindowComponent title="Manutenção de Série">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 3, required: true } }}
              id="abreviacao"
              inputProps={{ autoFocus: true }}
              isPrependLabel={true}
              label="Abreviação"
              labelWidth={defaultWidth}
              placeholder="Informe a Abreviação da Série."
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 20, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição."
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default SerieEditView;
